.visualiser {
  position: fixed;
  margin-left: 200px;
  width: 100%;
  height: 100%;
  background-color: #eaeaea;

  z-index: -1;
}

.container {
  display: flex;
  align-items: flex-end;

  position: fixed;
  margin-left: 15%;
  width: 83%;
  height: 100%;
  background-color: #eaeaea;
  padding: 0 1% 0 1%;
  z-index: -1;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 68%;
    margin-left: 30%;
  }
}

.bar {
  border: 1px solid #ff2e63;
  /* border-image-source: url(https://wallpaperstock.net/wallpapers/thumbs1/43896wide.jpg);
  border-image-slice: 60 30; */

  display: inline-block;
  /* width: 0px;
  height: 0px; */

  margin: 1px;
  transition: height 0.1s ease-out;
  transition: background-color 0.5s ease;
}

.bar-trans {
  border: 1px solid #ff2e63;
  transition: height 1s ease-in-out;
  display: inline-block;
  width: 0px;
  height: 0px;

  margin: 1px;
}
