@import url(https://fonts.googleapis.com/css?family=Quantico);
@import url(https://fonts.googleapis.com/css2?family=Knewave&family=Roboto&display=swap);
.sidebar {
  height: 100%;
  position: fixed;
  width: 15%;
  background-color: #e1e1e1;
  display: inline-block;
  text-align: left;
}

h1 {
  font-size: 35px;
  width: 100%;
  margin-top: 40px;
  margin-left: 10%;
  margin-bottom: 60px;
  font-family: "Knewave", cursive;
  background: -webkit-linear-gradient(#e92052, #ff2e63);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media only screen and (max-width: 600px) {
  .sidebar {
    width: 30%;
  }

  h1 {
    font-size: 28px;
  }
}

label {
  width: 100%;
  font-family: "Roboto", sans-serif;
  color: #ff2e63;
  margin-left: 10%;

  font-size: 12px;
}

.btn {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  width: 75%;
  height: 50px;
  color: #ff2e63;
  text-align: left;

  display: block;
  border: 1px none;
  position: relative;
  overflow: hidden;
  margin: 5px 0px 0px 25%;
  background: #e1e1e1;
}

.btn:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 0;
  background: #ff2e63;
  left: 0;
  bottom: 0;
  transition: all 0.3s;
  opacity: 0.3;
}

.btn:hover:before {
  width: 100%;
}

.btn-active:before {
  width: 100%;
}

.slider {
  -webkit-appearance: none !important;
  width: 85%;
  height: 15px;
  display: block;
  background: #eaeaea;
  border-radius: 10px;
  transition: all 0.3s ease;
  color: #e92052;
  margin: auto;
  justify-self: center;

  /* margin: 10px 25% 0 15%; */
}

.sliderOutput {
  font-family: "Roboto", sans-serif;
  color: black;
  font-size: 15px;
  opacity: 0.7;
  margin: 5px 0px 0px 58.5%;
}

.sliderContainer {
  width: 90%;
  padding-top: 10px;
  padding-bottom: 11px;
  background-color: black;

  border-radius: 5px;

  opacity: 0.7;
  margin: 0px 0px 0px 20%;
}

.btn-last {
  margin-bottom: 30px;
}

ul {
  padding: 0;
  margin: 0;
}

button:active,
button:focus,
input:active,
input:focus {
  box-shadow: none;
  outline: 0;
}

.dropdown ul {
  list-style-type: none;
}

.triangle {
  width: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 15px solid black;
  margin: 10px 0px 0px 58.5%;
  opacity: 0.7;
}

.visualiser {
  position: fixed;
  margin-left: 200px;
  width: 100%;
  height: 100%;
  background-color: #eaeaea;

  z-index: -1;
}

.container {
  display: flex;
  align-items: flex-end;

  position: fixed;
  margin-left: 15%;
  width: 83%;
  height: 100%;
  background-color: #eaeaea;
  padding: 0 1% 0 1%;
  z-index: -1;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 68%;
    margin-left: 30%;
  }
}

.bar {
  border: 1px solid #ff2e63;
  /* border-image-source: url(https://wallpaperstock.net/wallpapers/thumbs1/43896wide.jpg);
  border-image-slice: 60 30; */

  display: inline-block;
  /* width: 0px;
  height: 0px; */

  margin: 1px;
  transition: height 0.1s ease-out;
  transition: background-color 0.5s ease;
}

.bar-trans {
  border: 1px solid #ff2e63;
  transition: height 1s ease-in-out;
  display: inline-block;
  width: 0px;
  height: 0px;

  margin: 1px;
}



body {
  margin: 0;
}

